.search-input {
  border: none;
  margin-left: 4px;
  margin-right: 4px;
  padding: 4px;
}
.search-input:focus {
  border: none !important;
}

.search-input-container {
  display: flex;
  width: 100%;
  border-radius: 8px;
  border-width: 1px;
  padding-top: 12px;
  padding-bottom: 12px;
  border: 1px solid #97a3b6;
  overflow-x: auto;
}
